import React, { useState, useEffect } from 'react';
import {fetchData} from '../services/ApiServices';

function MyComponent(){
    const [data, setData] = useState<string | null>(null);

    useEffect(()=>{
        fetchData()
        .then(response => {
            setData(response);
        })
        .catch(console.error);
    }, []);

    return(
        <div>
          {data ? <p>{data}</p> : <p>Loading...</p>}
        </div>
    );
}

export default MyComponent;